"use client";
import fetcher from "@/utils/fetcher";
import { Button } from "@workspace/ui/components/button";
import { Input } from "@workspace/ui/components/input";
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from "@workspace/ui/components/card";
import { Badge } from "@workspace/ui/components/badge";
import { ArrowRight, ArrowLeft, ArrowLeftRight, ExternalLink, Loader2 } from "lucide-react";
import { useSearchParams } from "next/navigation";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import useSWR from "swr";
import { z } from "zod";

const searchSchema = z.string().min(1);

const useSearch = (query: string) => {
  const { data, error, isLoading } = useSWR(
    query ? `/api/search?query=${encodeURIComponent(query)}` : null,
    fetcher,
  );
  return { data, error, isLoading };
};

export default function SearchPage() {
  const [query, setQuery] = useState("");
  const router = useRouter();
  const searchParams = useSearchParams();
  
  useEffect(() => {
    const queryParam = searchParams.get("query");
    if (queryParam) {
      setQuery(queryParam);
    }
  }, [searchParams]);
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const result = searchSchema.safeParse(query);
    if (!result.success) {
      return;
    }
    router.push(`/search?query=${encodeURIComponent(query)}`);
  };
  
  // Get query parameters
  const currentQuery = searchParams.get("query") || "";
  const currentStart = parseInt(searchParams.get("start") || "0", 10);
  const maxResults = parseInt(searchParams.get("max_results") || "20", 10);
  
  // Fetch search results with pagination
  const { data, error, isLoading } = useSWR(
    currentQuery ? `/api/search?query=${encodeURIComponent(currentQuery)}&start=${currentStart}&max_results=${maxResults}` : null,
    fetcher
  );
  
  // Format date to be more readable
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });
  };
  
  // Extract arXiv ID from the full URL
  const getArxivId = (url: string) => {
    const matches = url.match(/\/abs\/([^\/]+)$/);
    return matches ? matches[1] : url;
  };
  
  // Normalize author data to always work with arrays
  const normalizeAuthors = (authorData: any) => {
    if (!authorData) return [];
    return Array.isArray(authorData) ? authorData : [authorData];
  };
  
  // Function to truncate summary text
  const truncateSummary = (text: string, maxLength = 250) => {
    if (text.length <= maxLength) return text;
    return text.slice(0, maxLength) + "...";
  };

  return (
    <main className="flex flex-col items-center px-4 max-w-7xl mx-auto">
      <div className="flex flex-col items-center text-center max-w-3xl mb-8 w-full">
        {/* Search Bar */}
        <form
          onSubmit={handleSubmit}
          className="flex items-center gap-2 border rounded-full p-1 w-full max-w-2xl mt-8 shadow-sm"
        >
          <Input
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Search..."
            className="border-none shadow-none focus-visible:ring-0"
          />
          <Button
            size="icon"
            className="rounded-full cursor-pointer"
            type="submit"
          >
            <ArrowRight />
          </Button>
        </form>
      </div>
      
      {/* Search Results */}
      <div className="w-full max-w-4xl">
        {currentQuery && (
          <div className="mb-4">
            <h2 className="text-xl font-medium">
              {isLoading 
                ? "Searching..." 
                : error 
                  ? "Error fetching results" 
                  : data && `${data.metadata.totalResults} results for "${currentQuery}"`}
            </h2>
          </div>
        )}
        
        {/* Loading State */}
        {isLoading && (
          <div className="flex flex-col justify-center items-center py-12">
            <div className="flex items-center">
              <Loader2 className="h-8 w-8 animate-spin text-primary" />
              <span className="ml-3 text-lg font-medium">Searching arXiv papers...</span>
            </div>
            <p className="text-sm text-muted-foreground mt-4 text-center max-w-md">
              Retrieving results from arXiv database. This may take a moment for complex queries 
              or when many results are found.
            </p>
          </div>
        )}
        
        {/* Error State */}
        {error && (
          <div className="rounded-md bg-red-50 p-4 my-4">
            <div className="flex">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="ml-3">
                <h3 className="text-sm font-medium text-red-800">
                  Error fetching search results
                </h3>
                <div className="mt-2 text-sm text-red-700">
                  <p>Please try again later or modify your search query.</p>
                  <p className="mt-1">If problems persist, the arXiv API may be temporarily unavailable.</p>
                </div>
              </div>
            </div>
          </div>
        )}
        
        {/* Results List */}
        {data && data.data && data.data.length > 0 ? (
          <div className="flex flex-col gap-4 mb-8">
            {data.data.map((paper) => (
              <Card key={paper.id} className="overflow-hidden">
                <CardHeader className="pb-2">
                  <CardTitle className="text-lg font-medium leading-tight">
                    <a 
                      href={paper.id} 
                      target="_blank" 
                      rel="noopener noreferrer"
                      className="hover:underline flex items-start"
                    >
                      <span className="mr-2">{paper.title.replace(/\\n\s+/g, ' ')}</span>
                      <ExternalLink className="h-4 w-4 flex-shrink-0 mt-1" />
                    </a>
                  </CardTitle>
                </CardHeader>
                <CardContent className="pb-2 text-sm">
                  <div className="flex flex-wrap gap-x-2 gap-y-1 items-center mb-3 text-muted-foreground">
                    <span>
                      Published: {formatDate(paper.published)}
                    </span>
                    <span className="hidden sm:inline">•</span>
                    <span>
                      arXiv: {getArxivId(paper.id)}
                    </span>
                  </div>
                  <div className="mb-3">
                    <div className="flex flex-wrap gap-1 mb-2">
                      {normalizeAuthors(paper.author).map((author, idx) => (
                          <Badge key={idx} variant="secondary" className="font-normal">
                            {author.name}
                          </Badge>
                        ))}
                    </div>
                  </div>
                  <p className="text-muted-foreground whitespace-pre-line">
                    {truncateSummary(paper.summary.replace(/\\n/g, ' '))}
                  </p>
                </CardContent>
                <CardFooter className="pt-2 flex justify-between">
                  <a 
                    href={paper.id} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="text-primary text-sm hover:underline"
                  >
                    View on arXiv
                  </a>
                  <a 
                    href={`${paper.id}.pdf`} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="text-primary text-sm hover:underline"
                  >
                    Download PDF
                  </a>
                </CardFooter>
              </Card>
            ))}
          </div>
        ) : (
          // No results state
          data && data.data && data.data.length === 0 && (
            <div className="text-center py-12">
              <h3 className="text-lg font-medium">No results found</h3>
              <p className="text-muted-foreground mt-2">
                Try adjusting your search query or explore different topics.
              </p>
            </div>
          )
        )}
        
        {/* Pagination */}
        {data && data.data && data.data.length > 0 && (
          <div className="flex flex-col md:flex-row justify-between items-center gap-4 my-8">
            <Button
              variant="outline"
              size="sm"
              onClick={() => {
                const newStart = Math.max(0, currentStart - maxResults);
                router.push(`/search?query=${encodeURIComponent(currentQuery)}&start=${newStart}&max_results=${maxResults}`);
              }}
              disabled={currentStart <= 0}
              className="flex items-center gap-1 w-full md:w-auto"
            >
              <ArrowLeft className="h-4 w-4" /> Previous
            </Button>
            
            <span className="text-sm text-muted-foreground">
              Showing {currentStart + 1} - {Math.min(
                currentStart + data.data.length,
                Number(data.metadata.totalResults)
              )} of {data.metadata.totalResults}
            </span>
            
            <Button
              variant="outline"
              size="sm"
              onClick={() => {
                const newStart = currentStart + maxResults;
                router.push(`/search?query=${encodeURIComponent(currentQuery)}&start=${newStart}&max_results=${maxResults}`);
              }}
              disabled={
                currentStart + data.data.length >= Number(data.metadata.totalResults)
              }
              className="flex items-center gap-1 w-full md:w-auto"
            >
              Next <ArrowRight className="h-4 w-4" />
            </Button>
          </div>
        )}
      </div>
    </main>
  );
}